import assert from "@brillout/assert";
import React from "react";

export { Comment };
export { Seperator };
export { Center };
export { TableRef };
export { SmallCaps };
export { ValueTable };
export { OpenSourceDesignText };
export { LsosCommonsTrust };
export { Highlight };

const COLOR_RED = "#a00";
const COLOR_GREEN = "#0a0";
const COLOR_ORANGE = "#e90";

function Comment() {
  return null;
}

function Seperator() {
  return <>&nbsp;&#124;</>;
}

function Yes() {
  return (
    <span style={{ color: COLOR_GREEN, fontSize: "1.9em", display: "block" }}>
      &#x2713;
    </span>
  );
}
function No() {
  return (
    <span style={{ color: COLOR_RED, fontSize: "1.6em", display: "block" }}>
      &#x2717;
    </span>
  );
}

function Gauge({ value }) {
  const borderRadius = 10;
  const width = compute_width();
  const color = compute_color(value);
  return (
    <div
      className="gauge"
      style={{
        height: 9,
        borderWidth: 1,
        borderStyle: "solid",
        borderColor: "#ddd",
        borderRadius,
        display: "flex",
        padding: "1px 2px",
      }}
    >
      <div
        style={{
          height: "100%",
          width,
          backgroundColor: color,
          borderRadius,
        }}
      />
    </div>
  );

  function compute_width() {
    assert(value > 0 && value < 100);
    return "calc(" + value / 100 + " * 100%)";
  }
}

function compute_color(value) {
  assert(value > 0 && value < 100);
  if (value < 50) {
    return COLOR_RED;
  }
  if (value >= 75) {
    return COLOR_GREEN;
  }
  return COLOR_ORANGE;
}

function Center({ children, style, ...props }) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        ...style,
      }}
      {...props}
    >
      {children}
    </div>
  );
}

function TableRef({ children }) {
  const title = children[0];
  const body = children.slice(1);
  return (
    <div style={{ marginBottom: 10, fontSize: "0.87em" }}>
      <i>{title}</i>: {body}
    </div>
  );
}

