function ValueTable({ entries, skip_links }) {
  return (
    <div className="values-table">
      <table>
        <thead>
          <tr>
            <TH></TH>
            <TH>Trans&shy;parent</TH>
            <TH>Fork&shy;able</TH>
            <TH>Access&shy;ible</TH>
            <TH>Sustai&shy;nable</TH>
            <TH>Inde&shy;pendent</TH>
          </tr>
        </thead>
        {entries.map((props, i) => (
          <TrEntry key={i} {...props} />
        ))}
      </table>
    </div>
  );

  function TrEntry({
    modelName,
    transparent,
    forkable,
    accessible,
    independent,
    sustainable,
  }) {
    return (
      <tr>
        <TD>{modelName}</TD>
        <TD>
          <Value val={transparent} />
        </TD>
        <TD>
          <Value val={forkable} />
        </TD>
        <TD>
          <Value val={accessible} />
        </TD>
        <TD>
          <Value val={sustainable} />
        </TD>
        <TD>
          <Value val={independent} />
        </TD>
      </tr>
    );
  }

  function TH({ children, ...props }) {
    assert(Object.keys(props).length === 0);
    if (!children) return <th />;
    const value_name = children;
    const child = skip_links ? (
      value_name
    ) : (
      <a href={get_value_link(value_name)}>{value_name}</a>
    );
    return <th align="center">{child}</th>;
  }

  function get_value_link(value_name) {
    assert(value_name.constructor === String);
    assert(!value_name.includes("&shy;"), { value_name });
    assert(!value_name.includes("%C2%AD"), { value_name });
    /*
    const value_link =
      "/values#" + value_name.toLowerCase().replace(/[^a-z]/gi, "");
    /*/
    const value_link = "/values";
    //*/
    return value_link;
  }

  function TD(props) {
    return <td align="center" {...props} />;
  }

  function Value({ val }) {
    assert(0 <= val && val <= 1);
    if (val === 1) {
      return <Yes />;
    }
    if (val === 0) {
      return <No />;
    }
    return <Gauge value={val * 100} />;
  }
}

function OpenSourceDesignText() {
  return (
    <p>
      Everything is designed with open source values in mind. Fees only apply to
      large companies and everything stays free for smaller companies and
      individuals.
    </p>
  );
}

function LsosCommonsTrust() {
  return (
    <>
      Although they are, from a legal perspective, forced to financially
      contribute, we tolerate and never sue the large companies who infringe the{" "}
      <SmallCaps>Lsos Commons</SmallCaps> license clause. Instead, we
      continuously remind large companies who forget to pay their fee, with
      kindness but insistence.
    </>
  );
}

function Highlight({ children }) {
  return (
    <Center>
      <div
        className="quote-font"
        style={{
          margin: 14,
          fontSize: "1.2em",
          color: "#666",
        }}
      >
        {children}
      </div>
    </Center>
  );
}
