import { SmallCaps } from "../components";

import React from "react";

export { ActionButtons };

function ActionButtons({ marginTop = 0, marginBottom } = {}) {
  const buttonStyle = {
    margin: "0 10px",
    flexGrow: 1,
    maxWidth: 170,
  };

  const emailSubject = encode("Lsos Donation Fund");

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        marginTop: 40 + marginTop,
        marginBottom,
      }}
    >
      <a
        className="lsos-button contact-address"
        style={buttonStyle}
        data-recipient="donate"
        data-subject={emailSubject}
        data-body={get_data_body_donate()}
        data-alreadyencoded={true}
        data-jslink={true}
      >
        <SmallCaps className="lsos-button--action-top">Support</SmallCaps>
        <span className="lsos-button--desc">your dependencies</span>
      </a>
      <div style={{ width: 20, height: 1, flexShrink: 9999 }}></div>
      <a
        className="lsos-button lsos-button--secondary contact-address"
        style={buttonStyle}
        data-recipient="receive"
        data-subject={emailSubject}
        data-body={get_data_body_receive()}
        data-alreadyencoded={true}
        data-jslink={true}
      >
        <SmallCaps className="lsos-button--action-top">Receive</SmallCaps>
        <span className="lsos-button--desc">support from companies</span>
      </a>
    </div>
  );
}

function get_data_body_donate() {
  return get_data_body(
    "We are interested in financially supporting our open source dependencies."
  );
}

function get_data_body_receive() {
  return get_data_body("I'm interested in receiving donations.");
}

function get_data_body(emailBody) {
  const emailBegin = "Hi,\n\n";
  const emailEnd = "\n\nKind regards.\n\n";
  return encode(emailBegin + emailBody + emailEnd);
}

function encode(str) {
  return encodeURIComponent(str);
}
